<script>

import { getContentTitle, getContentIcon, getMaterialIcon } from '../constants/utils'

export default {
  name: 'PageTable',
  data () { 
    return {
      selectedLevel: "",
      selectedLanguageActivities: "",
      selectedVocabulary: "",
      selectedGrammar: "",
    }
  },
  computed: {
    activeView () {
      return this.$store.state.activeView;
    },
    activeDestinations: function () {
      if (this.activeView === "table") {
        return this.$store.getters.activeDestinations
      } else {
        let list = this.$store.state.destinations;

        let selectedLevel = this.selectedLevel;
        let selectedLanguageActivities = this.selectedLanguageActivities;
        let selectedVocabulary = this.selectedVocabulary;
        let selectedGrammar = this.selectedGrammar;

        return list.filter((item) => {
          return (selectedLevel ? item.level === selectedLevel  : true) && 
            (selectedLanguageActivities ? item.content && item.content.S && item.content.S.includes(selectedLanguageActivities) : true) && 
            (selectedVocabulary ? item.content && item.content.W && item.content.W.includes(selectedVocabulary) : true) && 
            (selectedGrammar ? item.content && item.content.G && item.content.G.includes(selectedGrammar) : true)
        });
      }
    },
    levelList () {
      return this.$store.state.levelList;
    },
    languageActivitiesList () {
      return this.$store.state.languageActivitiesList;
    },
    vocabularyList () {
      return this.$store.state.vocabularyList;
    },
    grammarList () {
      return this.$store.state.grammarList;
    },

  },
  methods: {
    getContentTitle (type) {
      return getContentTitle(type);
    },
    getContentIcon (type) {
      return getContentIcon(type);
    },
    getMaterialIcon (item) {
      return getMaterialIcon(item);
    }
  }
}
</script>
