var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"info"},[_c('h1',[_vm._v("Information zu Seite")]),_c('ul',[_c('li',[_c('input',{attrs:{"type":"checkbox","checked":""}}),_c('i'),_c('h2',[_vm._v("Hintergrund")]),_c('p',[_vm._v("Während in der tschechisch-sächsischen Grenzregion vor einigen Jahren noch eine hohe Arbeitslosigkeit ein zentrales Problem darstellte, werden nun der demographische Wandel, der damit einhergehende Fachkräftemangel und die Sprachbarriere als Hauptherausforderungen gesehen (vgl. SMUL 2020: 86). Somit besteht ein zunehmender Bedarf an Hochschulabsolvent*innen, die sich für eine berufliche Zukunft in der Region entscheiden und neben Fachwissen auch über fachsprachliche Kompetenzen verfügen. Diese Tatsache muss sich auch im fachsprachlichen Unterricht an Hochschulen sowie weiterführenden Schulen widerspiegeln. Genau daran setzt das im Rahmen des vom Europäischen Fonds für regionale Entwicklung geförderte Projekt ProFit im Beruf – durch Sprach- und Fachsensibilisierung im Studium (ProFit v profesi - jazykovou a odbornou senzibilizací během studia) an. Das Kooperationsprojekt zwischen der Technischen Universität Liberec (TUL), Lead-Partner im Projekt, und der Westsächsischen Hochschule Zwickau (WHZ) hat das übergreifende Ziel, Studierende mit flexibel einsetzbaren Fachkenntnissen aber auch (fach-)sprachlichen und interkulturellen Kompetenzen auszurüsten. Damit soll die Verbesserung ihrer Chancen für einen erfolgreichen Einstieg in den regionalen, grenznahen Arbeitsmarkt einhergehen. Die dazu entwickelten und durchgeführten Projektaktivitäten mit dem Ziel einer fachbezogenen und für die Region zugeschnittene Sprachvermittlung waren vielfältig. An dieser Stelle sei auf ein zentrales Zitat von Albert Raasch (2004: 8) aus seinen Reflexionen zu Nachbarsprachen in Grenzregionen verwiesen: „Unterricht braucht Lehrmaterial, das die grenzüberschreitenden Beziehungen, die Kultur der Region jenseits der Grenze, die identitätsstiftende Kooperation thematisiert.“ Inwiefern sich die explizite Thematisierung des tschechisch-sächsischen Wirtschaftsraums in Fremdsprachenkursen, wie z. B. dem Wirtschaftsdeutschunterricht an Hochschulen in den Nachbarregionen, inhaltlich exemplarisch umgesetzt werden kann, zeigt diese Webseite..")])]),_c('li',[_c('input',{attrs:{"type":"checkbox","checked":""}}),_c('i'),_c('h2',[_vm._v("Themen")]),_c('p',[_vm._v("In gemeinsamer Absprache wurden von den zehn Themenfelder für Erstellung der Lehr- und Lernmaterialien festgelegt..")]),_c('p',[_vm._v("1. Deutschland u. Tschechien als Import- und Exportländer")]),_c('p',[_vm._v("2. Regionale Wirtschafts- und Kooperationsbeziehungen und ihre Spezifika")]),_c('p',[_vm._v("3. Traditionelle Branchen im Liberecer und Zwickauer Gebiet – Stand und Perspektiven")]),_c('p',[_vm._v("4. Wirtschaftspotenzial der Grenzregion, u. a. regionale Produkte und Vermarktung")]),_c('p',[_vm._v("5. Grenzüberschreitendes Projektmanagement")]),_c('p',[_vm._v("6. Nachhaltigkeit: Infrastrukturen, Erneuerbare Energien, Elektromobilität, Umwelt")]),_c('p',[_vm._v("7. Moderne Technologien in der Wirtschaft")]),_c('p',[_vm._v("8. Lebensqualität und Tourismus als Wirtschaftsfaktoren in der Grenzregion")]),_c('p',[_vm._v("9. Messen und Ausstellungen")]),_c('p',[_vm._v("10. Praktisches Projektmanagement im Studium")]),_c('p',[_vm._v("Die zehn festgelegten Themenfelder wurden in den sechs Landschaften zusammengefasst. Dabei bildet das zugrundeliegende Konzept der „Landschaften“ als Metapher die Dynamik von Entwicklungsprozessen in Wirtschaft, Natur und Gesellschaft ab. Die gewählten thematischen Schwerpunkte sollen die an den Kursen teilnehmenden Studierenden sowohl für Fragestellungen und Probleme, aber auch für Innovationen und Problemlösungen sensibilisieren, die den Alltag der Menschen und die Berufswelt der Zukunft maßgeblich bestimmen werden. Grundlage der Übungssequenzen zu den verschiedenen Lernorten ist meist ein authentischer Text und/ oder ein Video. Anhand der Vielfalt der festgelegten Themenbereiche decken dieses ein breites Spektrum an Branchen ab. Die darauf abgestimmten digitalen Übungen zielen insbesondere darauf ab, das Leseverstehen und Hör- bzw. Hörsehverstehen zu trainieren.")])]),_c('li',[_c('input',{attrs:{"type":"checkbox","checked":""}}),_c('i'),_c('h2',[_vm._v("Nutzung")]),_c('p',[_vm._v("Ganz im Sinne des Kennenlernens und näheren Beschäftigung mit der Region sind die digitalen Lerneinheiten in den einzelnen thematischen „Landschaften“ in eine Karte eingebettet, die die Grenzregion abbildet. Die „Lernorte“ sind meist thematisch verbunden mit einem realen Ort auf der Karte. Beim Erkunden der Karte werden durch das Anklicken eines Lernorts über einen Informationsblock jeweils das Sprachniveau der Übung, die relevanten Sprachhandlungen, das damit verbundene Wortfeld und grammatische Schwerpunktthema transparent gemacht. Des Weiteren sind teilweise auch Links zur weiteren Recherche angegeben. Zudem beinhalten alle Landschaften PDF-Dokumente mit thematisch passenden Unterrichtsvorschlägen für Lehrende.")]),_c('p',[_vm._v("Legende.")]),_c('p',[_vm._v("Alle Materialien zeichnen sich durch eine flexible Einsetzbarkeit aus, da sie keiner stringenten sprachlichen Progression folgen und sind als Ergänzung und nicht als Ersatz eines kurstragenden Lehrwerks zu sehen. Weitere Hinweise zur Entstehung, Einsatzmöglichkeiten und Erfahrungen mit dem Material in Hartinger (2021): Wirtschaftsdeutsch regional und digital vermitteln – Ansätze im Rahmen des Projekts „ProFit im Beruf – ProFit v profesi“.")])]),_c('li',[_c('input',{attrs:{"type":"checkbox","checked":""}}),_c('i'),_c('h2',[_vm._v("Kontakt")]),_c('p',[_vm._v("Haben Sie Fragen zu dieser Seite? Oder sind Sie selbst im Sprachlernbereich in der tschechisch-sächsischen Grenzregion tätig und haben selbst Lern-/Lehrmaterial entwickelt, das sich auf die Region bezieht? Helfen Sie mit die Landschaften zu bereichern, indem Sie es uns mit Angabe des vorgesehenen Sprachniveaus zusenden. Das kann ein Arbeitsblatt im PDF-Format sein oder auch ein Link zu einer Online-Übung. Gern pflegen wir Ihr Material in die passende Landschaft ein.")]),_c('p',[_vm._v("E-Mail: profitimberuf@gmail.com")])])])])}]

